import React from 'react';

export async function getServerSideProps() {
  const response = await fetch('https://api.genius.com/songs/378195', {
    method: 'GET',
    headers: {
      'Authorization': 'Bearer zBLa6-8lpJshJ_tWuB6MHgw1ke6-JwR8UhISBjm5y3h1e5c4XbQUXD0NO9UBuFRX',
    },
  });

  const data = await response.json();
  const songData = data.response?.song?.title || 'Song not found';

  return {
    props: {
      songData,
    },
  };
}

export default function Songs({ songData }) {
  return (
    <div>
      <h1>{songData}</h1>
    </div>
  );
}